class InvoiceComponent {
    constructor(activatedRoute, paymentService, router) {
        this.activatedRoute = activatedRoute;
        this.paymentService = paymentService;
        this.router = router;
        this.isLoader = false;
    }
    ngOnInit() {
        this.activatedRoute.params.subscribe(params => {
            let id = params['id'];
            if (id) {
                this.get(id);
            }
        });
    }
    get(id) {
        console.log(id);
        this.isLoader = true;
        this.paymentService.getById(id).subscribe((res) => {
            if (res.status == '200') {
                console.log(res.data);
                this.bill = res.data;
                this.isLoader = false;
            }
            else {
                this.router.navigate(['app/payment']);
            }
        });
    }
    getSubTotal() {
        let subTotal = 0;
        this.bill.items.forEach(a => subTotal += (a.price * a.quantity));
        return subTotal;
    }
    applyDiscount() {
        return this.getSubTotal() - (this.getSubTotal() * (this.bill.discount / 100));
    }
    getDiscount() {
        return this.getSubTotal() * (this.bill.discount / 100);
    }
}


